<script>
import { goto } from '@sveltech/routify';
import Header from '../components/Header.svelte';
import Setting from '../components/Setting.svelte';
import ErrorMessage from '../components/ErrorMessage.svelte';
import PXL from '../utils/PXL.js';
import { getAPIInfo, setAPIInfo } from '../utils/config.js';

const pxl = new PXL(getAPIInfo().apibase, getAPIInfo().apikey);

let parsed = new URL(window.location.href);
let base = parsed.searchParams.get('base');
let key = parsed.searchParams.get('key');
if (base !== null && key !== null) {
  setAPIInfo({
    apibase: base,
    apikey: key,
  }, pxl);
  $goto(parsed.origin);
}
let redirect = parsed.searchParams.get('redirect');
if (redirect !== null) {
  $goto(redirect);
}
</script>

<Header />
<slot scoped={{ pxl }}></slot>
<Setting pxl={pxl} />
<ErrorMessage />
