import { errorMessage } from './stores.js';

class PXL {
  constructor(apiBase, apikey) {
    this.set(apiBase, apikey);
  }

  set(apiBase, apikey) {
    this.apiBase = apiBase;
    this.apikey = apikey;
  }

  detect(url) {
    return this.request(`/detect?url=${encodeURIComponent(url)}`, json => json.list || json);
  }

  detect_by_file(element) {
    const formData = new FormData();
    formData.append('file', element.files[0]);
    return this.request(`/detect`, json => json.list || json, {
      method: 'POST',
      body: formData,
    }, true);
  }

  analyze(url) {
    return this.request(`/analyze?url=${encodeURIComponent(url)}&lang=en`, json => json.list || json);
  }

  analyze_by_file(element) {
    const formData = new FormData();
    formData.append('file', element.files[0]);
    return this.request(`/analyze?lang=en`, json => json.list || json, {
      method: 'POST',
      body: formData,
    }, true);
  }

  search(regionId, gender, c2, c3, c4, attrs, flex_mode, count) {
    let url = `/search/${regionId}`;
    if (this.apiBase.indexOf('/v1') > -1) {
      let searchCategory = gender | c2;
      const flex_query = {};
      if (c3 !== undefined) {
        flex_query["sub_category"] = c3;
      }
      // FIXME: Support attribute search
      if (attrs && attrs.length > 0) {
        flex_query["attributes"] = attrs;
      }
      if (typeof regionId === 'string' && regionId.indexOf('_') < 0 && regionId.indexOf(',') < 0) {  // tonic
        return this.request(`${url}?count=${count}`, json => json.list[parseInt(Object.keys(json.list)[0])]);
      }
      return this.request(`${url}?gender=${gender}&category=${searchCategory}&count=${count}&mode=${flex_mode}`,
        json => json.list[parseInt(Object.keys(json.list)[0])],
        {
          method: 'POST',
          body: JSON.stringify(flex_query),
        }
      );
    }
    return this.request(`${url}?genders=${gender}&category=${c4 ? c4 : c3 ? c3 : c2}&count=${count}&mode=${flex_mode}`);
  }

  tonic(url, count) {
    return this.request(`/search?url=${encodeURIComponent(url)}&count=${count}`);
  }

  recommend(regionId, mode, gender, category, count) {
    let path = `/stylist/${regionId}`;
    let query = `genders=${gender}&category=${category}`;
    if (this.apiBase.indexOf('/v1') > -1) {
      path = `/recommend/stylist/${regionId}`;
      query = `category=${gender | category}`;
    }
    return this.request(`${path}?${query}&count=${count}&mode=${mode}`, json => json.list || json);
  }

  request(query, parse, options, isFile) {
    const is_apikey = this.apikey.length > 56;
    const headers = {
      [is_apikey ? "ApiKey" : "bucketname"]: this.apikey,
    };
    if (isFile !== true) {
      headers["Content-Type"] = "application/json";
    }
    if (parse === undefined) {
      parse = json => json;
    }
    return fetch(`${this.apiBase}${query}`, {
      ...options,
      headers,
    }).then(async res => {
      if (!res.ok) {
        throw await res.json();
      }
      return res.json();
    }).then(json => {
      if (json.status === false) {
        throw json.message || 'API status is false.';
      }
      return parse(json);
    }).catch(err => {
      if (query.indexOf('/analyze') === 0) {
        console.log('Analyze API failed.');
        return;
      }      
      errorMessage.set(err);
    });
  }
}

export default PXL;
