import { APPS } from './constant.js';

let appkey = localStorage.getItem('app') || Object.entries(APPS)[0][0];
let apibase = localStorage.getItem('apibase') || Object.entries(APPS)[0][1].apibase;
let apikey = localStorage.getItem('apikey') || '';
let demoImages = localStorage.getItem('demo-images') || '';

export function getApp() {
  return appkey;
}

export function setApp(appname) {
  appkey = appname;
  localStorage.setItem("app", appkey);
}

export function getAPIInfo() {
  return {
    apibase: apibase,
    apikey: apikey,
  };
}

export function setAPIInfo(apiInfo, i) {
  apibase = apiInfo.apibase;
  apikey = apiInfo.apikey;
  localStorage.setItem('apibase', apibase);
  localStorage.setItem('apikey', apikey);
  i.set(apibase, apikey);
}

export function getDEMOImages() {
  return demoImages;
}

export function setDEMOImages(str) {
  demoImages = str
  localStorage.setItem("demo-images", str);
}
