<script>
import { errorMessage } from '../utils/stores.js';
import { LABELS } from '../utils/constant.js';
import ProductItem from './ProductItem.svelte';

export let type;
export let results;

function getCategoryName(label) {
  if (typeof label === 'string' && label.indexOf('.') > -1) {
    return LABELS[label]['en-US'];
  }
  return LABELS[LABELS[label]]['en-US'];
}

function apiError(err) {
  errorMessage.set(err);
  return '';
}
</script>

{#await results}
  <div id="result-list" class="loading"></div>
{:then json}
  <div id="result-list">
    {#if type === 'recommend' && json !== undefined && Object.keys(json).length > 0}
      {#each Object.keys(json) as label}
        <h2>{getCategoryName(label)}</h2>
        <div class="grid-list">
          {#each json[label] as product}
            <ProductItem product={product} />
          {/each}
        </div>
      {/each}
    {:else if type === 'recommend-multi' && json !== undefined && json.length > 0}
      {#each json as set, i}
        <h2>set {i + 1}</h2>
        <div class="grid-list">
          {#each set as product}
            <ProductItem product={product} />
          {/each}
        </div>
      {/each}
    {:else if type === 'search' && json !== undefined && json.length > 0}
      <div class="grid-list">
        {#each json as product}
          <ProductItem product={product} />
        {/each}
      </div>
    {:else if json !== undefined}
      No results found.
    {/if}
  </div>
{:catch err}
  {apiError(err)}
{/await}
